import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useToast } from "@/hooks/use-toast";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import { addDoc, collection, serverTimestamp, getDocs, query, where, updateDoc, doc } from 'firebase/firestore';
import { db } from '@/firebase';
import { Loader2, List, ArrowRight, CheckCircle2, Clock } from 'lucide-react';
import { format } from 'date-fns';
import { Badge } from "@/components/ui/badge";

interface PendingOperationsDialogProps {
  isOpen: boolean;
  onClose: () => void;
}

export const PendingOperationsDialog: React.FC<PendingOperationsDialogProps> = ({ isOpen, onClose }) => {
  const { user } = useFirebaseAuth();
  const { toast } = useToast();
  const [view, setView] = useState<'add' | 'list'>('add');
  const [loading, setLoading] = useState(false);
  const [actionLoading, setActionLoading] = useState<string | null>(null);
  
  // Form State
  const [senderWalletNumber, setSenderWalletNumber] = useState('');
  const [receiverWalletNumber, setReceiverWalletNumber] = useState('');
  const [amount, setAmount] = useState('');
  const [suspensionDate, setSuspensionDate] = useState('');
  const [retrievalDate, setRetrievalDate] = useState('');

  // List State
  const [pendingOps, setPendingOps] = useState<any[]>([]);

  // Fetch Operations
  const fetchPendingOperations = async () => {
    if (!user) return;
    setLoading(true);
    try {
      const q = query(
        collection(db, 'pendingOperations'),
        where('userId', '==', user.uid)
      );
      const snap = await getDocs(q);
      const docs = snap.docs.map(d => ({ id: d.id, ...d.data() }));
      // Sort in memory to avoid index requirement
      docs.sort((a: any, b: any) => {
        const dateA = a.createdAt?.toDate?.() || new Date(0);
        const dateB = b.createdAt?.toDate?.() || new Date(0);
        return dateB - dateA;
      });
      setPendingOps(docs);
    } catch (error) {
      console.error("Error fetching pending operations:", error);
      toast({
        title: "خطأ",
        description: "فشل تحميل العمليات المعلقة",
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  // Effect to fetch when view changes to list
  useEffect(() => {
    if (view === 'list' && isOpen) {
      fetchPendingOperations();
    }
  }, [view, isOpen]);

  // Reset view when dialog closes
  useEffect(() => {
    if (!isOpen) {
      setView('add');
      setSenderWalletNumber('');
      setReceiverWalletNumber('');
      setAmount('');
      setSuspensionDate('');
      setRetrievalDate('');
    }
  }, [isOpen]);

  const handleAdd = async () => {
    if (!user) return;

    setLoading(true);
    try {
      // Save to 'pendingOperations' collection instead of 'userTransactions'
      await addDoc(collection(db, 'pendingOperations'), {
        userId: user.uid,
        senderWalletNumber,
        receiverWalletNumber,
        amount: amount ? parseFloat(amount) : 0,
        suspensionDate,
        retrievalDate,
        status: 'pending', // Default status
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      });

      toast({
        title: "تم الإضافة",
        description: "تم إضافة العملية المعلقة بنجاح",
      });
      
      // Reset form
      setSenderWalletNumber('');
      setReceiverWalletNumber('');
      setAmount('');
      setSuspensionDate('');
      setRetrievalDate('');
      
    } catch (error) {
      console.error(error);
      toast({
        title: "خطأ",
        description: "حدث خطأ أثناء الإضافة",
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const handleRetrieve = async (id: string) => {
    setActionLoading(id);
    try {
      const docRef = doc(db, 'pendingOperations', id);
      await updateDoc(docRef, {
        status: 'retrieved',
        updatedAt: serverTimestamp()
      });

      // Update local state
      setPendingOps(prev => prev.map(op => 
        op.id === id ? { ...op, status: 'retrieved' } : op
      ));

      toast({
        title: "تم التحديث",
        description: "تم تغيير حالة العملية إلى مسترد",
      });
    } catch (error) {
      console.error("Error updating operation status:", error);
      toast({
        title: "خطأ",
        description: "فشل تحديث حالة العملية",
        variant: "destructive"
      });
    } finally {
      setActionLoading(null);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[500px]" dir="rtl">
        <DialogHeader>
          <DialogTitle className="text-right">
            {view === 'add' ? 'إضافة عملية معلقة' : 'سجل العمليات المعلقة'}
          </DialogTitle>
        </DialogHeader>

        {view === 'add' ? (
          <>
            <p className="text-sm text-muted-foreground text-right mb-2 px-1">
              هذا قسم خاص بإضافة العمليات المعلقة بين المحافظ ومنتظر رد العملية من الشركة
            </p>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="senderWallet" className="text-right">
                  المحفظة المرسلة
                </Label>
                <Input
                  id="senderWallet"
                  value={senderWalletNumber}
                  onChange={(e) => setSenderWalletNumber(e.target.value)}
                  className="col-span-3"
                  placeholder="رقم المحفظة المرسلة (اختياري)"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="receiverWallet" className="text-right">
                  المحفظة المستقبلة
                </Label>
                <Input
                  id="receiverWallet"
                  value={receiverWalletNumber}
                  onChange={(e) => setReceiverWalletNumber(e.target.value)}
                  className="col-span-3"
                  placeholder="رقم المحفظة المستقبلة (اختياري)"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="amount" className="text-right">
                  المبلغ المعلق
                </Label>
                <Input
                  id="amount"
                  type="number"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                  className="col-span-3"
                  placeholder="0.00 (اختياري)"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="suspensionDate" className="text-right">
                  تاريخ التعليق (اختياري)
                </Label>
                <Input
                  id="suspensionDate"
                  type="date"
                  value={suspensionDate}
                  onChange={(e) => setSuspensionDate(e.target.value)}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="retrievalDate" className="text-right">
                  تاريخ الاسترداد (اختياري)
                </Label>
                <Input
                  id="retrievalDate"
                  type="date"
                  value={retrievalDate}
                  onChange={(e) => setRetrievalDate(e.target.value)}
                  className="col-span-3"
                />
              </div>
            </div>
            
            <div className="flex flex-col gap-3 mt-4">
              <Button 
                onClick={handleAdd} 
                disabled={loading} 
                className="w-full bg-orange-500 hover:bg-orange-600 text-white"
              >
                {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                إضافة
              </Button>
              
              <Button 
                variant="outline" 
                onClick={() => setView('list')}
                className="w-full border-orange-200 text-orange-700 hover:bg-orange-50 hover:text-orange-800"
              >
                <List className="ml-2 h-4 w-4" />
                العمليات المعلقة
              </Button>
            </div>
          </>
        ) : (
          <>
            <div className="py-4 max-h-[60vh] overflow-y-auto space-y-3">
              {loading ? (
                <div className="flex justify-center p-8">
                  <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
                </div>
              ) : pendingOps.length > 0 ? (
                pendingOps.map((op) => (
                  <div key={op.id} className="border rounded-lg p-3 bg-card text-card-foreground shadow-sm relative overflow-hidden">
                    {/* Status Badge */}
                    <div className="absolute top-3 left-3">
                      {op.status === 'retrieved' ? (
                        <Badge className="bg-green-100 text-green-700 border-green-200 hover:bg-green-100">
                          <CheckCircle2 className="w-3 h-3 mr-1" />
                          مسترد
                        </Badge>
                      ) : (
                        <Badge className="bg-orange-100 text-orange-700 border-orange-200 hover:bg-orange-100">
                          <Clock className="w-3 h-3 mr-1" />
                          معلق
                        </Badge>
                      )}
                    </div>

                    <div className="flex justify-between items-start mb-2 pl-20">
                      <div>
                        <span className="font-bold text-lg block">{op.amount || 0} ج.م</span>
                        {op.senderWalletNumber && (
                          <span className="text-sm text-muted-foreground block">
                            من: {op.senderWalletNumber}
                          </span>
                        )}
                        {op.receiverWalletNumber && (
                          <span className="text-sm text-muted-foreground block">
                            إلى: {op.receiverWalletNumber}
                          </span>
                        )}
                        {/* Fallback for old data */}
                        {!op.senderWalletNumber && !op.receiverWalletNumber && op.walletNumber && (
                          <span className="text-sm text-muted-foreground block">{op.walletNumber}</span>
                        )}
                      </div>
                    </div>
                    
                    <div className="text-xs text-muted-foreground grid grid-cols-2 gap-2 mt-3 pb-2 border-b border-dashed mb-3">
                      <div>
                        <span className="block font-semibold text-gray-500">تاريخ التعليق:</span>
                        {op.suspensionDate}
                      </div>
                      <div>
                        <span className="block font-semibold text-gray-500">تاريخ الاسترداد:</span>
                        {op.retrievalDate}
                      </div>
                    </div>

                    {/* Action Button */}
                    {(op.status !== 'retrieved') && (
                      <Button 
                        size="sm" 
                        variant="outline" 
                        className="w-full border-green-200 text-green-700 hover:bg-green-50 hover:text-green-800 mt-1 h-8 text-xs"
                        onClick={() => handleRetrieve(op.id)}
                        disabled={actionLoading === op.id}
                      >
                        {actionLoading === op.id ? (
                          <Loader2 className="h-3 w-3 animate-spin" />
                        ) : (
                          <>
                            <CheckCircle2 className="mr-1 h-3 w-3" />
                            تم الاسترداد
                          </>
                        )}
                      </Button>
                    )}
                  </div>
                ))
              ) : (
                <div className="text-center text-muted-foreground p-8">
                  لا توجد عمليات معلقة
                </div>
              )}
            </div>
            
            <div className="mt-4">
              <Button 
                variant="ghost" 
                onClick={() => setView('add')}
                className="w-full flex items-center justify-center gap-2"
              >
                <ArrowRight className="h-4 w-4" />
                العودة للإضافة
              </Button>
            </div>
          </>
        )}
      </DialogContent>
    </Dialog>
  );
};
